:root {
  --spinTime: 1s;
  --bg1: #1b1925;
}

body {
  background-color: var(--bg1);
}

.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
}

.fan {
  width: 50%;
  height: 50%;
  transform: translate(-50%, -50%);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.spin2 {
  animation: App-logo-spin 2s linear 0s infinite normal;
  animation-play-state: paused;
  width: fit-content;
  height: fit-content;
}

.spin2:hover {
  animation-play-state: running;
}

.spin1 {
  position: absolute;
  animation: App-logo-spin 4s linear 0s infinite reverse;
  width: fit-content;
  height: fit-content;
}

.spinWrapper {
  transition: transform 4s ease-in-out;
}

.spin1:hover .spinWrapper {
  transform: rotate(-360deg);
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}